.slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	user-select: none;
	-webkit-touch-callout: none;
	-khtml-user-select: none;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
		.slick-arrow {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 99;
		cursor: pointer;
		&:before {
			background-color: rgba(#29abe2, 0.6);
			@include size(50px);
			border-radius: 50%;
			line-height: 50px;
			color: #626262;
			margin: 0;

		}
		&.slide-prev {
			left: 15px;
			&:before {
				content: "";
				display: block;
				background-image:url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAHCAYAAADEUlfTAAAAJUlEQVR42mNgAILy8vL/DLgASBKnApgkVgXIkhgKiNKJ005s4gDLbCZBiSxfygAAAABJRU5ErkJggg==");
				background-repeat: no-repeat;
				background-position: center;
			}

		}
		&.slide-next {
			right: 15px;
			&:before {
				content: "";
				display: block;
				background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAcAAAAHCAYAAADEUlfTAAAAJUlEQVR42mNgAILy8vL/DLgASBKnApgkVgXIkhgKiNKJ005s4gDLbCZBiSxfygAAAABJRU5ErkJggg==");
				background-repeat: no-repeat;
				background-position: center;
			}
		}
	}
}
.slick-list {
	position: relative;
	display: block;
	overflow: hidden;
	margin: 0;
	padding: 0;
	&:focus {
		outline: none;
	}
	&.dragging {
		cursor: pointer;
		cursor: hand;
	}
}
.slick-slider .slick-track,
.slick-slider .slick-list {
	transform: translate3d(0, 0, 0);
}
.slick-track {
	position: relative;
	top: 0;
	left: 0;
	display: block;
	margin-left: auto;
	margin-right: auto;
	&:after {
		clear: both;
	}
}
.slick-track:before,
.slick-track:after {
	display: table;
	content: '';
}
.slick-loading {
	.slick-track {
		visibility: hidden;
	}
	.slick-slide {
		visibility: hidden;
	}
}
.slick-slide {
	display: none;
	float: left;
	height: 100%;
	min-height: 1px;
	img {
		display: block;
	}
	&.slick-loading {
		img {
			display: none;
		}
	}
	&.dragging {
		img {
			pointer-events: none;
		}
	}
}
[dir='rtl'] {
	.slick-slide {
		float: right;
	}
}
.slick-initialized {
	.slick-slide {
		display: block;
	}
}
.slick-vertical {
	.slick-slide {
		display: block;
		height: auto;
		border: 1px solid transparent;
	}
}
.slick-arrow {
	&.slick-hidden {
		display: none;
	}
}