.header-nav {
	position: relative;
}
#nav {
	width: 100%;

	@include media("<=desktop") {
		width: 100%;
		padding: 0;
		transition: all .25s linear;
		transform: translateY(-100%);
		background-color: lighten($color-brand, 15%);
	}

	ul {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
		align-items: center;
		@extend %listreset;

		@include media("<=desktop") {
			flex-wrap: wrap;
			padding-left: $container-pading-left;
			padding-right: $container-pading-right;
		}

		@include media("<=tablet") {
			padding: 0 30px;
		}

		@include media("<=phone") {
			padding: 0 15px;
		}
	}

	li {
		position: relative;
		list-style: none;
		//fix bug edge browser
		list-style-image: url(data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7);
		padding: 10px 0;

		@include media("<=desktop") {
			width: 100%;
			margin: 0;
		}

		a {
			text-decoration: none;
		}

		.drop {
			position: absolute;
			z-index: 3;
			display: none;
			width: 160px;
			top: 100%;
			left: 50%;
			transform: translateX(-50%);
			background: lighten($color-brand, 15%);

			@include media("<=desktop") {
				position: static;
				width: 100%;
				transform: translateX(0);

				.drop {
					padding: 0px 10px 10px 10px;
				}
			}

			ul {
				display: block;
				position: relative;
				border: 1px solid #000;
				padding: 0px;
				text-align: center;

				@include media("<=desktop") {
					text-align: left;
					background: lighten($color-brand, 15%);
					margin-left: -1px;
					margin-right: -1px;
				}
			}

			li {
				padding: 0;
				margin: 0;
			}

			a {
				display: block;
				padding: 10px;

				@include media("<=desktop") {
					margin: 0;
					padding: 10px 33px 10px 10px;
				}
			}
		}

		a {
			@include media("<=desktop") {
				display: block;
				padding: 0 25px 0 0;
			}
		}

		&.hover {
			>.drop {
				display: block;
				cursor: pointer;

				@include media("<=desktop") {
					display: none;
				}

				.hover {
					.drop {
						left: 100%;
						transform: translateX(0);
						top: -1px;
					}
				}

				a {
					// color:$black;
				}
			}
		}

		&:last-child {
			margin-right: 0;

			@include media("<=desktop") {
				margin-right: auto;
			}

			.drop {
				@include media("<=ipad-pro") {
					left: auto;
					right: 0;
					transform: translateX(0);
				}
			}
		}

		&:nth-last-child(2),
		&:last-child {
			&.hover {
				.drop {
					.hover {
						.drop {
							@include media("<=ipad-pro") {
								left: auto;
								right: 100%;
							}
						}
					}
				}
			}
		}
	}

	.drop-opener {
		@include media("<=desktop") {
			width: 40px;
			height: 40px;
			position: absolute;
			right: -5px;
			top: 8px;
			transition: all 0.15s linear;
			z-index: 999;

			&:before {
				content: "";
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 10px 0 10px 15px;
				border-color: transparent transparent transparent #333;
				transition: all 0.15s linear;
			}
		}
	}

	.has-drop-down {
		.drop-opener {
			&:before {
				@include media(">desktop") {
					content: "";
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: 0;
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 10px 0 10px 15px;
					border-color: transparent transparent transparent #fff;
					transition: all 0.15s linear;
				}
			}
		}

		&.hover {
			&>.drop-opener {
				&:before {
					@include media(">desktop") {
						transform: translateY(-50%) rotate(90deg);
					}
				}
			}
		}
	}

	.drop {
		.drop-opener {
			top: 0;
			right: 0;
		}
	}

	.actives {
		>.drop-opener {
			&:before {
				transform: translate(-50%, -50%) rotate(90deg);
			}
		}
	}
}

.nav-drop {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	z-index: 5;

	@include media("<=desktop") {
		height: 0;
		overflow: hidden;
		padding: 0;
		position: absolute;
		left: 0;
		right: 0;
		top: calc(100% + 30px);
		transition: all .25s linear;
		max-height: 0;
	}
}

.nav-opener {
	display: none;
	position: absolute;
	right: 0px;
	top: 50%;
	transform: translateY(-50%);
	width: 40px;
	height: 40px;
	border-radius: 3px;
	text-indent: -9999px;
	overflow: hidden;


	@include media("<=desktop") {
		display: block;
	}
	@include media("<=tablet") {
		right: 100px;
	}

	&:before,
	&:after,
	span {
		background: $black;
		border-radius: 4px;
		position: absolute;
		top: 50%;
		left: 12%;
		right: 12%;
		height: 4px;
		margin-top: -2px;
		transition: all 0.2s linear;
	}

	&:before,
	&:after {
		content: "";
		top: 30%;
	}

	&:after {
		top: 70%;
	}

	&:hover {
		opacity: .8;
	}
}

.nav-active {
	.nav-opener {
		span {
			opacity: 0;
		}

		&:before,
		&:after {
			transform: rotate(45deg);
			top: 50%;
			left: 12%;
			right: 12%;
		}

		&:after {
			transform: rotate(-45deg);
		}
	}

	#nav {
		transform: translateY(0);
	}

	.nav-drop {
		max-height: 4000px;
		height: auto;
	}
}

#nav {
	.menu-main {
		@include media("<=desktop") {
			padding-left: 30px;
			padding-right: 30px;
		}
		&>li {
			@include media(">desktop") {
				@include liX(20px);
			}
			&>a {
				@include media("<=desktop") {
					padding: 10px 25px 10px 0;
				}
			}
		}
	}
}