/* radio */
.jcf-radio {
	vertical-align: middle;
	display: inline-block;
	position: relative;
	overflow: hidden;
	cursor: default;
	border-radius: 50%;
	margin: 0 3px 0 0;
	height: 22px;
	width: 22px;
	border: 2px solid #cccccc;
	background-color: #ffffff;
	-webkit-transition: border-color .2s ease;
	transition: border-color .2s ease;

	span {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		width: 10px;
		height: 10px;
		background: #fff;
		border-radius: 50%;
		transition: border-color .2s ease, opacity .2s ease, visibility .2s ease;
	}

	input[type="radio"] {
		position: absolute;
		height: 100%;
		width: 100%;
		border: 0;
		margin: 0;
		left: 0;
		top: 0;
	}

	&.jcf-checked {
		background-color: #65b161;
		border-color: #fff;

		span {
			visibility: visible;
			opacity: 1;
		}

		&.jcf-focus {
			border-color: #fff !important;
		}
	}
}

/* checkbox */
.jcf-checkbox {
	vertical-align: middle;
	display: inline-block;
	position: relative;
	overflow: hidden;
	cursor: default;
	background: #fff;
	border: 1px solid #777;
	margin: 0 3px 0 0;
	height: 16px;
	width: 16px;
	-webkit-transition: border-color .2s ease;
	transition: border-color .2s ease;
}

.jcf-checkbox span {
	position: absolute;
	opacity: 0;
	visibility: hidden;
	height: 7px;
	width: 10px;
	top: 50%;
	left: 50%;
	margin: -7px 0 0 -6px;
	border: 3px solid #777;
	border-width: 0 0 3px 3px;
	-webkit-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	transform: rotate(-45deg);
	-webkit-transition: border-color .2s ease, opacity .2s ease, visibility .2s ease;
	transition: border-color .2s ease, opacity .2s ease, visibility .2s ease;
}

html .jcf-checkbox span {
	margin: -4px 0 0 -5px;
}

.jcf-checkbox input[type="checkbox"] {
	position: absolute;
	width: 100%;
	height: 100%;
	border: 0;
	margin: 0;
	left: 0;
	top: 0;
}

.jcf-checkbox.jcf-checked span {
	visibility: visible;
	opacity: 1;
}

/* button */
.jcf-button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	overflow: hidden;
	border: 1px solid #777;
	background: #fff;
	margin: 0 10px 10px 0;
	padding: 5px 10px;
}

.jcf-button .jcf-real-element {
	position: absolute;
	min-height: 100%;
	min-width: 100%;
	outline: none;
	opacity: 0;
	padding: 0;
	margin: 0;
	border: 0;
	bottom: 0;
	right: 0;
	left: 0;
	top: 0;
}

/* file */
.jcf-file {
	display: inline-block;
	white-space: nowrap;
	position: relative;
	overflow: hidden;
	background: #fff;
}

.jcf-file .jcf-real-element {
	position: absolute;
	font-size: 200px;
	height: 200px;
	margin: 0;
	right: 0;
	top: 0;
}

.jcf-file .jcf-fake-input {
	display: inline-block;
	text-overflow: ellipsis;
	white-space: nowrap;
	vertical-align: top;
	overflow: hidden;
	border: 1px solid #777;
	padding: 0 8px;
	font-size: 13px;
	line-height: 28px;
	height: 28px;
	width: 125px;
}

.jcf-file .jcf-upload-button {
	display: inline-block;
	vertical-align: top;
	white-space: nowrap;
	overflow: hidden;
	border: 1px solid #777;
	margin: 0 0 0 -1px;
	padding: 0 10px;
	line-height: 28px;
	height: 28px;
}

/* scrollbars */
.jcf-scrollable-wrapper {
	box-sizing: content-box;
	position: relative;
}

.jcf-scrollbar-vertical {
	position: absolute;
	cursor: default;
	background: #e3e3e3;
	width: 14px;
	bottom: 0;
	right: 0;
	top: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-dec,
.jcf-scrollbar-vertical .jcf-scrollbar-inc {
	background: #bbb;
	height: 14px;
	width: 14px;
	left: 0;
	top: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-inc {
	top: auto;
	bottom: 0;
}

.jcf-scrollbar-vertical .jcf-scrollbar-handle {
	background: #888;
	height: 1px;
	width: 14px;
}

.jcf-scrollbar-horizontal {
	position: absolute;
	background: #e3e3e3;
	right: auto;
	top: auto;
	left: 0;
	bottom: 0;
	width: 1px;
	height: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-dec,
.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
	display: inline-block;
	vertical-align: top;
	overflow: hidden;
	background: #bbb;
	height: 14px;
	width: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-inc {
	left: auto;
	right: 0;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-slider {
	display: inline-block;
	position: relative;
	height: 14px;
}

.jcf-scrollbar-horizontal .jcf-scrollbar-handle {
	position: absolute;
	background: #888;
	height: 14px;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-handle {
	visibility: hidden;
}

.jcf-scrollbar.jcf-inactive .jcf-scrollbar-dec,
.jcf-scrollbar.jcf-inactive .jcf-scrollbar-inc {
	background: #e3e3e3;
}

/* select */
.jcf-select {
	display: inline-block;
	vertical-align: top;
	position: relative;
	border: 1px solid #777;
	background: #fff;
	margin: 0 0 12px;
	min-width: 150px;
	height: 26px;
}

.jcf-select select {
	z-index: 1;
	left: 0;
	top: 0;
}

.jcf-select .jcf-select-text {
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	cursor: default;
	display: block;
	font-size: 13px;
	line-height: 26px;
	margin: 0 35px 0 8px;
}

.jcf-select .jcf-select-opener {
	position: absolute;
	text-align: center;
	background: #aaa;
	width: 26px;
	bottom: 0;
	right: 0;
	top: 0;
}

body>.jcf-select-drop {
	position: absolute;
	margin: -1px 0 0;
	z-index: 9999;
}

body>.jcf-select-drop.jcf-drop-flipped {
	margin: 1px 0 0;
}

.jcf-select .jcf-select-drop {
	position: absolute;
	margin-top: 0px;
	z-index: 9999;
	top: 100%;
	left: -1px;
	right: -1px;
}

.jcf-select .jcf-drop-flipped {
	bottom: 100%;
	top: auto;
}

.jcf-select.jcf-compact-multiple {
	max-width: 220px;
}

.jcf-select.jcf-compact-multiple .jcf-select-opener:before {
	display: inline-block;
	padding-top: 2px;
	content: '...';
}

.jcf-select-drop .jcf-select-drop-content {
	border: 1px solid #f00;
}

.jcf-select-drop.jcf-compact-multiple .jcf-hover {
	background: none;
}

.jcf-select-drop.jcf-compact-multiple .jcf-selected {
	background: #e6e6e6;
	color: #000;
}

.jcf-select-drop.jcf-compact-multiple .jcf-selected:before {
	display: inline-block;
	content: '';
	height: 4px;
	width: 8px;
	margin: -7px 5px 0 -3px;
	border: 3px solid #777;
	border-width: 0 0 3px 3px;
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
	-ms-filter: "progid:DXImageTransform.Microsoft.Matrix(M11=0.7071067811865483, M12=0.7071067811865467, M21=-0.7071067811865467, M22=0.7071067811865483, SizingMethod='auto expand')";
}

/* multiple select styles */
.jcf-list-box {
	overflow: hidden;
	display: inline-block;
	border: 1px solid #b8c3c9;
	min-width: 200px;
	margin: 0 15px;
}

/* select options styles */
.jcf-list {
	display: inline-block;
	vertical-align: top;
	position: relative;
	background: #fff;
	line-height: 14px;
	font-size: 12px;
	width: 100%;
}

.jcf-list .jcf-list-content {
	vertical-align: top;
	display: inline-block;
	overflow: auto;
	width: 100%;
}

.jcf-list ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

.jcf-list ul li {
	overflow: hidden;
	display: block;
}

.jcf-list .jcf-overflow {
	overflow: auto;
}

.jcf-list .jcf-option {
	white-space: nowrap;
	overflow: hidden;
	cursor: default;
	display: block;
	padding: 5px 9px;
	color: #656565;
	min-height: 14px;
	height: 1%;
}

.jcf-list .jcf-disabled {
	background: #fff !important;
	color: #aaa !important;
}

.jcf-select-drop .jcf-hover,
.jcf-list-box .jcf-selected {
	background: #e6e6e6;
	color: #000;
}

.jcf-list .jcf-optgroup-caption {
	white-space: nowrap;
	font-weight: bold;
	display: block;
	padding: 5px 9px;
	cursor: default;
	color: #000;
}

.jcf-list .jcf-optgroup .jcf-option {
	padding-left: 30px;
}

/* other styles */
.jcf-textarea {
	border: 1px solid #b8c3c9;
	box-sizing: content-box;
	display: inline-block;
	position: relative;
}

.jcf-textarea .jcf-scrollbar-horizontal {
	display: none;
	height: 0;
}

.jcf-textarea textarea {
	padding: 8px 10px;
	border: none;
	margin: 0;
}

.jcf-textarea .jcf-resize {
	position: absolute;
	text-align: center;
	cursor: se-resize;
	background: #e3e3e3;
	font-weight: bold;
	line-height: 15px;
	text-indent: 1px;
	font-size: 12px;
	height: 15px;
	width: 14px;
	bottom: 0;
	right: 0;
}

.jcf-textarea .jcf-resize:before {
	border: 1px solid #000;
	border-width: 0 1px 1px 0;
	display: block;
	margin: 4px 0 0 3px;
	width: 6px;
	height: 6px;
	content: '';
}

/* number input */
.jcf-number {
	display: inline-block;
	position: relative;
	height: 32px;
}

.jcf-number input {
	-moz-appearance: textfield;
}

.jcf-number input::-webkit-inner-spin-button,
.jcf-number input::-webkit-outer-spin-button {
	-webkit-appearance: none;
}

.jcf-number input {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	border: 1px solid #777;
	padding: 3px 27px 3px 7px;
	margin: 0;
	height: 100%;
}

.jcf-number .jcf-btn-dec,
.jcf-number .jcf-btn-inc {
	position: absolute;
	background: #aaa;
	width: 20px;
	height: 15px;
	right: 1px;
	top: 1px;
}

.jcf-number .jcf-btn-dec {
	top: auto;
	bottom: 1px;
}

.jcf-number .jcf-btn-dec:hover,
.jcf-number .jcf-btn-inc:hover {
	background: #e6e6e6;
}

.jcf-number.jcf-disabled .jcf-btn-dec:hover,
.jcf-number.jcf-disabled .jcf-btn-inc:hover {
	background: #aaa;
}

.jcf-number .jcf-btn-dec:before,
.jcf-number .jcf-btn-inc:before {
	position: absolute;
	content: '';
	width: 0;
	height: 0;
	top: 50%;
	left: 50%;
	margin: -6px 0 0 -4px;
	border: 4px solid #aaa;
	border-color: transparent transparent #000 transparent;
}

.jcf-number .jcf-btn-dec:before {
	margin: -1px 0 0 -4px;
	border-color: #000 transparent transparent transparent;
}

.jcf-number.jcf-disabled .jcf-btn-dec:before,
.jcf-number.jcf-disabled .jcf-btn-inc:before,
.jcf-number .jcf-btn-dec.jcf-disabled:before,
.jcf-number .jcf-btn-inc.jcf-disabled:before {
	opacity: 0.3;
}

.jcf-number.jcf-disabled input {
	background: #ddd;
}

/* range input */
.jcf-range {
	display: inline-block;
	min-width: 200px;
	margin: 0 10px;
	width: 130px;
}

.jcf-range .jcf-range-track {
	margin: 0 20px 0 0;
	position: relative;
	display: block;
	height: 10px;
}

.jcf-range .jcf-range-wrapper {
	position: relative;
	background: #e5e5e5;
	border-radius: 5px;
	display: block;
	margin: 5px 0;
}

.jcf-range.jcf-vertical {
	min-width: 0;
	width: auto;
}

.jcf-range.jcf-vertical .jcf-range-wrapper {
	margin: 0;
	width: 10px;
	height: auto;
	padding: 20px 0 0;
}

.jcf-range.jcf-vertical .jcf-range-track {
	height: 180px;
	width: 10px;
}

.jcf-range.jcf-vertical .jcf-range-handle {
	left: -5px;
	top: auto;
}

.jcf-range .jcf-range-display-wrapper {
	position: absolute;
	min-height: 10px;
	min-width: 10px;
	left: 10px;
	right: 10px;
}

.jcf-range .jcf-range-display {
	border-radius: 5px;
	position: absolute;
	background: #888;
	width: 10px;
	height: 10px;
}

.jcf-range .jcf-range-display.jcf-range-min {
	margin: 0 0 0 -10px;
	padding: 0 0 0 10px;
}

.jcf-range .jcf-range-display.jcf-range-max {
	margin: 0 -10px 0 0;
	padding: 0 10px 0 0;
}

.jcf-range.jcf-vertical .jcf-range-display-wrapper {
	left: auto;
	right: auto;
	top: 10px;
	bottom: 10px;
}

.jcf-range.jcf-vertical .jcf-range-display.jcf-range-min {
	margin: 0 0 -10px;
	padding: 0 0 10px;
}

.jcf-range.jcf-vertical .jcf-range-display.jcf-range-max {
	margin: -10px 0 0;
	padding: 10px 0 0;
}

.jcf-range .jcf-range-handle {
	position: absolute;
	background: #aaa;
	border-radius: 19px;
	width: 19px;
	height: 19px;
	margin: -4px 0 0;
	z-index: 1;
	top: 0;
	left: 0;
}

.jcf-range .jcf-range-mark {
	position: absolute;
	overflow: hidden;
	background: #000;
	width: 1px;
	height: 3px;
	top: -7px;
	margin: 0 0 0 9px;
}

.jcf-range.jcf-vertical .jcf-range-mark {
	margin: 0 0 9px;
	left: 14px;
	top: auto;
	width: 3px;
	height: 1px;
}

.jcf-range .jcf-active-handle {
	z-index: 2;
}

.jcf-range.jcf-focus .jcf-active-handle {
	border: 1px solid #f00;
	margin: -5px 0 0 -1px;
}

.jcf-range.jcf-disabled {
	background: none !important;
	opacity: 0.3;
}

/* common styles */
.jcf-disabled {
	background: #ddd !important;
}

.jcf-focus,
.jcf-focus * {
	border-color: #f00 !important;
}

/* label demo styles */
.jcf-label-active {
	font-weight: bold;
}

/* select demo styles */
.jcf-select-color .jcf-select-text .jcf-option-color {
	font-weight: bold;
}

.jcf-select-text .jcf-option-red,
.jcf-list .jcf-option-red {
	color: #d00;
}

.jcf-select-text .jcf-option-green,
.jcf-list .jcf-option-green {
	color: #0d0;
}

.jcf-select-text .jcf-option-blue,
.jcf-list .jcf-option-blue {
	color: #00d;
}

.jcf-select-drop .jcf-option-hideme {
	display: none;
}

.jcf-select.jcf-select-present {
	line-height: 48px;
	height: 48px;
	background: #fff;
}

.jcf-select.jcf-select-present .jcf-select-text img {
	vertical-align: top;
	width: 32px;
	height: auto;
	margin: 7px 5px 0 0;
}

.jcf-select-present .jcf-select-text {
	line-height: 48px;
}

.jcf-select-present .jcf-list {
	font-style: italic;
	line-height: 48px;
}

.jcf-select-present .jcf-list img {
	vertical-align: middle;
	margin: 0 5px 0 0;
}

//new
.jcf-list {
	ul {
		li {
			padding-left: 0;
		}
	}
}